import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			topiclist: [],
			page: 1,
			limit: 100,
			totalPages: 1
		}
	},
	onLoad: function(options) {
		this.gettopiclist();
	  },
	  onPullDownRefresh() {
		this.gettopiclist(true);
	  },
	methods: {
	  gettopiclist: function(flag) {
		  uni.showLoading({
			title: '加载中...',
		  });
		  let that = this;
		  api.httpget('/topic/list?page=1&limit='+this.limit).then(function(res) {
			if (res.data.errno === 0) {
				that.topiclist = res.data.data.list;
				that.totalPages = res.data.data.pages
			}
			if(flag) uni.stopPullDownRefresh()
			uni.hideLoading();
		  })
		}
	}
}
